import React from 'react'
import Peer from 'skyway-js';

// P2PPeer
// keyAPIdebug3
const peer = new Peer({
  key: "77cd23a7-a204-435d-9886-765953d75c73",
  debug: 3,
});

const Video = (props) => {
  let localStream;
  console.log(navigator.userAgent);

  // 
  // getUserMediaAPI
  // navigatormediaDevicegetUserMedia()
  navigator.mediaDevices.getUserMedia({video: true, audio: true})
    // getUserMediastreamvideo
    .then( stream => {
    // video
    const videoElm = document.getElementById('my-video');
    videoElm.srcObject = stream;
    videoElm.play();
    // (localStream)
    localStream = stream;
  }).catch( error => {
    // 
    console.error('mediaDevice.getUserMedia() error:', error);
    return;
  });

  // 
  peer.on('open', () => {
    document.getElementById('my-id').textContent = peer.id;
  });

  peer.on('error', err => {
    alert('');
  });

  // 
  // const finishVideo = () => {
  //   return(
  //   
  //   )
  // };

  peer.on('close', () => {
    alert('');
  });

  // 
  // callpeer
  // mediaConnectionPeer
  // mediaConnectioncall
  peer.on('call', mediaConnection => {
    // mediaConnectionanser
    // localStream 
    mediaConnection.answer(localStream);
    setEventListener(mediaConnection);
  });

  // (handleCall)
  const handleCall = () => {
    // ID
    const theirID = document.getElementById('their-id').value;
    // video
    const mediaConnection = peer.call(theirID, localStream);
    setEventListener(mediaConnection);
  };

  // 
  const setEventListener = mediaConnection => {
    mediaConnection.on('stream', stream => {
      // video
      const videoElm = document.getElementById('their-video')
      videoElm.srcObject = stream;
      videoElm.play();
    });
  }

  // html
  return (
    <div>
      {/*  */}
      <video id="my-video" width="70%" autoPlay muted playsInline></video>
      {/*  */}
      <video id="their-video" width="400px" autoPlay muted playsInline></video>
      
      {/* ID */}
      <p id='my-id'></p>
      {/* PeerID */}
      <input id="their-id"></input>
      {/*  */}
      <button id="make-call" onClick={handleCall}></button>
      {/*  */}
      {/* <button onClick={finishVideo}></button> */}
    </div>
  );
}

export default Video;
