// 
import React from 'react';
import { signInWithPopup } from 'firebase/auth';
import { auth, provider } from '../firebase';
import { useAuthState } from 'react-firebase-hooks/auth';
import Sidebar from './Sidebar';
import google_logo from '../img/googles-new-logo-5078286822539264.3-hp2x.gif';
// 
import { BrowserRouter as Router, Route, Routes  } from 'react-router-dom';

function Signin() {
  // user
  const [user] = useAuthState(auth);

  return (
    <div>
      <div className='signin'>
        {/* usertrueUserInfoSignOutButton
        falseSignInButton */}
        {user ? (
          <div class='SidebarWapper'>
            <UserInfo />
            <SignOutButton />
            <Sidebar />
          </div>
        ):(
          <SignInButton />
          )}
      </div>
    </div>
  );
}

export default Signin;

// 
function SignInButton (){
  const signInWithGoogle = () => {
    // firebase(firebase)
    signInWithPopup(auth, provider);
  };

  return(
    <div className='SignInWrapper'>
      <img src={google_logo}/>
      <button onClick={signInWithGoogle}>
        google
      </button>
    </div>
  );
}

// (confirmOK)
function SignOutButton (){
  const ComfirmSignOut = () => {
    let result = window.confirm('');
    result && auth.signOut()
  };

  return(
    // firebase(firebase)
    <button class='buttondaca' onClick={ComfirmSignOut}>
      <p className='SignOutButton'></p>
    </button>
  );
}

// 
function UserInfo (){
  return(
      <div className='userInfo'>
        <img src={auth.currentUser.photoURL} alt='google'></img>
        <p>{auth.currentUser.displayName}</p>
      </div>
  );
}
